
.title{
    font-size: 23px;
    font-weight: 700px;
}

.daily-item {
    background-color: #f5f5f5;
    border-radius: 15px;
    height: 40px;
    margin: 5px;
    display: flex;
    align-items: center;
    cursor: pointer;
    font-size: 14px;
    padding: 5px 20px;
}

.icon-small {
    width: 40px; 
}

.day{
   color: #212121;
   flex: 1 1;
   font-weight: 600px;
   margin-left: 15px; 
}

.description{
    flex: 1 1;
    margin-right: 15px;
    text-align: right;
}

.min-max {
    color: #757575;
}

.daily-detail-grid{
    grid-row-gap: 0px;
    grid-column-gap: 15px;
    row-gap: 0px;
    column-gap: 15px;
    display: grid;
    flex: 1 1;
    grid-template-columns: auto auto;
    padding: 5px 15px;
}

.daily-detail-grid-item{
    display: flex;
    height: 30px;
    justify-content: space-around;
    align-items: center;
}

.daily-detail-grid-item label:first-child {
    color:#757575;
}

.daily-detail-grid-item label:second-child {
    color:#212121;
}



